<template>
  <div class="home">
    <b-container>
      <b-tabs small>
        <b-tab title="TABLE">
          <Crud
            :apidata="apiData"
            :form="form"
            v-on:deletedata="deleteData"
            v-on:updatedata="updateFormData"
            v-on:formdata="submitEditData"
          />
        </b-tab>
        <b-tab title="INSERT NEW">
          <Insert
            :form="form"
            v-on:formdata="submitFormData"
            v-on:resetdata="resetFromData"
          />
        </b-tab>
      </b-tabs>
    </b-container>
  </div>
</template>

<script>
import Crud from "@/components/Crud.vue";
import Insert from "@/components/Insert.vue";
import { mapActions } from "vuex";

export default {
  name: "home",
  components: {
    Crud,
    Insert
  },
  data() {
    return {
      form: {
        userId: "",
        id: "",
        title: "",
        completed: "",
        options: [
          { value: true, text: "TRUE" },
          { value: false, text: "FALSE" }
        ]
      }
    };
  },
  computed: {
    apiData() {
      return this.$store.state.apiData;
    }
  },
  methods: {
    ...mapActions([
      "getApiData",
      "insertNewData",
      "deleteApiData",
      "updateApiData"
    ]),

    submitFormData: function(value) {
      this.form = value;
      this.insertNewData(value);
    },

    resetFromData: function(value) {
      this.form.userId = "";
      this.form.id = "";
      this.form.title = "";
      this.form.completed = "";

      // TODO::Remove Extra API Call
      // WHY THIS CALL::This function not only reset the form data but also api data
      this.getApiData();
    },

    deleteData: function(value) {
      this.deleteApiData(value);
    },

    submitEditData: function(value) {
      this.updateApiData(value);
    },

    updateFormData: function(value) {
      this.form.userId = value.userId;
      this.form.id = value.id;
      this.form.title = value.title;
      this.form.completed = value.completed;
    }
  },
  mounted() {
    this.getApiData();
  }
};
</script>
<style scoped lang="scss">
.tabs {
  padding-top: 15px;
  .tab-pane {
    outline: none;
  }
}
</style>
