




































































import { Component, Prop, Vue, Emit } from "vue-property-decorator";

@Component
export default class Crud extends Vue {
  @Prop()
  private apidata!: Object;

  @Prop()
  private form!: Object;

  public deleteApiData(id: any) {
    this.$emit("deletedata", id);
  }

  public updateData(item: any) {
    this.$emit("updatedata", item);
  }

  public onSubmit(evt: any) {
    evt.preventDefault();
    this.$emit("formdata", this.form);
  }
}
