import Vue from "vue";
import Vuex from "vuex";
import axios from "axios"

Vue.use(Vuex, axios);

export default new Vuex.Store({
  state: {
    apiData: []
  },
  mutations: {
    setApiData: (state, data) => {
      state.apiData = data;
    },
    spliceApiData: (state, id) => {
      state.apiData.splice(id, 1);
    }
  },
  actions: {
    getApiData: content => {
      axios
        .get('https://jsonplaceholder.typicode.com/todos')
        .then( data => {
          content.commit('setApiData', data.data);
        })
        .catch( (error: any) => {
          console.log(error);
        })
    },
    insertNewData: (content, newData) => {
      axios
        .post('https://jsonplaceholder.typicode.com/todos', {
          userId: newData.userId,
          id: newData.id,
          title: newData.title,
          completed: newData.completed
        }) 
        .then( data => {
          alert("INSERT SUCCESS");
        })
        .catch( (error: any) => {
          console.log(error);
        })
    },
    deleteApiData: (content, id) => {
      axios
        .delete('https://jsonplaceholder.typicode.com/todos' + id) 
        .then( (data: any) => {
          content.commit('spliceApiData', id);
        })
        .catch( (error: any) => {
          console.log(error);
        })

      // As delete request faild
      // this function only called for making a delete preview
      content.commit('spliceApiData', id);
    },
    updateApiData: (content, updateData) => {
      axios
        .put('https://jsonplaceholder.typicode.com/todos/' + updateData.id, {
          userId: updateData.userId,
          id: updateData.id,
          title: updateData.title,
          completed: updateData.completed
        }) 
        .then( data => {
          alert("UPDATE SUCCESS");
        })
        .catch( (error: any) => {
          console.log(error);
        })
    }
  },
  modules: {}
});
